<template>
    <div class="famousTeacher">
      <div class="header">
            <span @click="back" class="back">
                <img src="../assets/img/back.png">
            </span>
            <span class="usersTitle">名师堂</span>
            <span class="back"></span>
      </div>
      <ul class="teachers">
        <li class="teacher" v-for="(teacher, index) in teachers" :key="index" @click="showInfo(index)">
          <span class="headPic">
            <img :src="teacher.headPic">
          </span>
          <span class="info">
            <span class="name">{{ teacher.name }}</span>
            <br>
            <span class="introduce">{{ teacher.introduce }}</span>
            <br>
            <span class="lessonsAndStudents">
              <span class="lessons">讲述课程数:{{ teacher.lessonCounts }}套</span>
              <span class="students">学员数:{{ teacher.studentsCounts}}人</span>
            </span>
          </span>
          <span class="next">
            <img src="../assets/img/next.png">
          </span>
        </li>
      </ul>
      <div class="notice">更多名师即将入驻</div>
      <div class="greyBackground"></div>
    </div>
</template>

<script>
export default {
  name: 'famousTeacher',
  data () {
    return {
      teachers: [{
        headPic: require('../assets/img/headTest.png'), name: '康伟', introduce: '陈氏太极拳名家', lessonCounts: 4, studentsCounts: 1668
      }, {
        headPic: require('../assets/img/headTest.png'), name: '康伟', introduce: '陈氏太极拳名家', lessonCounts: 4, studentsCounts: 1668
      }, {
        headPic: require('../assets/img/headTest.png'), name: '康伟', introduce: '陈氏太极拳名家', lessonCounts: 4, studentsCounts: 1668
      }, {
        headPic: require('../assets/img/headTest.png'), name: '康伟', introduce: '陈氏太极拳名家', lessonCounts: 4, studentsCounts: 1668
      }, {
        headPic: require('../assets/img/headTest.png'), name: '康伟', introduce: '陈氏太极拳名家', lessonCounts: 4, studentsCounts: 1668
      }, {
        headPic: require('../assets/img/headTest.png'), name: '康伟', introduce: '陈氏太极拳名家', lessonCounts: 4, studentsCounts: 1668
      }, {
        headPic: require('../assets/img/headTest.png'), name: '康伟', introduce: '陈氏太极拳名家', lessonCounts: 4, studentsCounts: 1668
      }, {
        headPic: require('../assets/img/headTest.png'), name: '康伟', introduce: '陈氏太极拳名家', lessonCounts: 4, studentsCounts: 1668
      }, {
        headPic: require('../assets/img/headTest.png'), name: '康伟', introduce: '陈氏太极拳名家', lessonCounts: 4, studentsCounts: 1668
      }]
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    showInfo (index) {
      this.$router.push('/home/index/famousTeacherInfo')
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.426667rem;
  }

  .greyBackground {
    position: fixed;
    width: 100%;
    height: 100vh;
    background: #f5f5f5;
    z-index: -1;
    top: 0;
    left: 0;
  }

  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: fixed;
    font-size: 0.48rem;
    background: #fff;
    z-index: 99;
    top: 0;
    left: 0;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back,
  .next {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .teachers {
    padding: 0 0.4rem;
    margin-top: 1.5rem;
    background: #fff;
    list-style: none;
  }

  .teacher {
    display: flex;
    padding: 0.37333rem 0;
    border-bottom: 1px solid #f5f5f5;
    position: relative;
  }

  .teacher:last-child {
    border: none;
  }

  .headPic {
    display: flex;
    width: 1.6rem;
    height: 1.6rem;
    align-items: center;
    justify-content: center;
  }

  .headPic img {
    border-radius: 0.8rem;
  }

  .info {
    margin-left: 0.4rem;
    flex-grow: 1;
    margin-top: 0.2rem;
  }

  .name,
  .introduce,
  .lessonsAndStudents {
    width: 100%;
    display: inline-block;
    text-align: start;
  }

  .name,
  .introduce {
    color: #333333;
  }

  .introduce {
    font-size: 0.37333rem;
    margin-top: 0.2rem;
  }

  .lessonsAndStudents span {
    font-size: 0.34667rem;
    color: #666666;
  }

  .students {
    margin-left: 0.8rem;
  }

  .next {
    display: flex;
    position: absolute;
    right: 0;
    top: 0.8rem;
  }

  .notice {
    margin: 0.53333rem 0 3.7rem 0;
    font-size: 0.4rem;
    color: #c0c0c0;
    letter-spacing: 0.01rem;
  }
</style>



